@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@300&family=Poppins:wght@300;600&display=swap);
.RPButton {
  color: var(--Black10);
  background-color: var(--Black90);
  border-radius: 0.24em;
  padding: 1em;
  border-color: var(--Black10);
  border-style: none;
  transition: 0.2s;
}

.RPButton:hover {
  background-color: var(--Black80);
  transition: 0.2s;
}

.RPCard {
  color: var(--Black10);
  background-color: var(--Black90);
  border-radius: 0.24em;
  padding: 2em;
}

.app-grid-columns {
  display: grid;
  grid-template-columns: repeat(12, [col-start] 1fr);
  grid-gap: 20px;
  gap: 20px;
}

.app-grid-container {
  display: grid;
}

.app-grid-item {
  margin-bottom: 16px;
}

/* --- Globals --- */

:root {
  --Primary: #a11d6a;
  --Secondary: #951fff;
  --Linear: linear-gradient(to right, var(--Primary), var(var(--Secondary)));
  --Error: #e00248;

  --Black: #262626;
  --Black90: #333333;
  --Black80: #444444;
  --Black75: #666666;
  --Black50: #a6a6a6;
  --Black25: #e6e6e6;
  --Black10: #f2f2f2;
}

.Monospace {
  font-size: 0.875rem;
  font-weight: 300;
  font-family: "JetBrains Mono", monospace;
  color: #e6e6e6;
  color: var(--Black25);
}

.Paragraph {
  font-size: 0.875rem;
  font-weight: 300;
  font-family: "Poppins", sans-serif;
  color: #e6e6e6;
  color: var(--Black25);
}

h1 {
  font-size: 7.063rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

h2 {
  font-size: 5rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

h3 {
  font-size: 3.563rem;
  font-weight: 300;
  font-family: "Poppins", sans-serif;
  color: white;
}

h4 {
  font-size: 2.5rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

h5 {
  font-size: 1.75rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

html,
body {
  background: #262626;
  background: var(--Black);
  scrollbar-width: none;
  scroll-behavior: smooth;
  height: 100vh;
}

* {
  -ms-overflow-style: none;
}
::-webkit-scrollbar {
  display: none;
}

